<template>
    <div class="sign-list">
        <sign-item
            v-for="(sign, index) in signedList"
            :key="index"
            :index="index"
            :sign="sign" />
    </div>
</template>
<script>
import SignItem from './SignItem';

export default {
    name: 'SignList',
    components: {
        SignItem,
    },
    props: {
        signDays: {
            type: Number,
            default: 0,
        },
        signList: {
            type: Array,
            default: () => [],
        },
    },
    computed: {
        signedList() {
            const signDays = this.signDays;
            const signedList = this.signList;

            if (!signDays || !signedList.length) return signedList;

            for (let i = 0; i <= signDays; i += 1) {
                if (i < signDays) {
                    signedList[i].signed = true;
                } else if (signedList[i]) {
                    signedList[i].tomorrow = true;
                }
            }

            return signedList;
        },
    },
};
</script>
<style scoped>
.sign-list {
    display: flex;
    flex-wrap: wrap;
}

.sign-list .sign-item {
    margin: 9px 5.5px;
}
</style>
