<template>
    <div :class="['sign-item']">
        <div
            v-if="tomorrow"
            class="tomorrow">明日领取</div>
        <div :class="[type, { signed }]">{{ award }}</div>
        <div class="text">{{ text }}</div>
    </div>
</template>
<script>
export default {
    name: 'SignItem',
    props: {
        index: {
            type: Number,
            default: 0,
        },
        sign: {
            type: Object,
            default: () => ({}),
        },
    },
    computed: {
        award() {
            return this.sign.award || 0;
        },
        signed() {
            return !!this.sign.signed;
        },
        tomorrow() {
            return !!this.sign.tomorrow;
        },
        style() {
            return this.sign.style || 0;
        },
        type() {
            return this.style ? 'gift' : 'coin';
        },
        text() {
            return this.signed ? '已签' : `${this.index + 1}天`;
        },
    },
};
</script>
