<template>
    <div class="sign">
        <div
            class="back"
            @click="handleBackClick" />
        <div class="content">
            <div class="container list">
                <div class="title">已签<span>{{ signDays }}</span>/{{ totalDays }}天</div>
                <sign-list
                    :sign-days="signDays"
                    :sign-list="signList" />
            </div>
            <div class="container rules">
                <div class="title">签到规则</div>
                <ul>
                    <li
                        v-for="(rule, index) in rules"
                        :key="index"
                        v-html="rule" />
                </ul>
            </div>
        </div>
        <div
            class="footer"
            @click="goInvite" />
    </div>
</template>
<script>
import {
    openCommonWebViewByPath,
    quitCurrentWebview,
} from '@/utils/jsApiV2/client';
import { getConfigByKey } from '@/api/common';
import { getSignInfo } from '@/api/sign';
import { apolloSignRulesKey, defaultRules } from './config';
import SignList from './SignList';

export default {
    name: 'Sign',
    components: {
        SignList,
    },
    data() {
        return {
            signDays: 0,
            signList: [],
            rules: [],
        };
    },
    created() {
        this.getSignInfo();
        this.getRules();

        this.$o2o({ e_c: '任务', e_a: '曝光', e_n: '签到挑战页面曝光' });

        this.$root.hidePlaceholder();
    },
    computed: {
        totalDays() {
            return this.signList ? this.signList.length : 30;
        },
    },
    methods: {
        getSignInfo() {
            getSignInfo()
                .then((res) => {
                    this.signDays = res.signDays;
                    this.signList = res.elements;
                })
                .catch(this.$error);
        },
        getRules() {
            getConfigByKey({
                key: apolloSignRulesKey,
            })
                .then((res) => {
                    this.rules = res.data;
                })
                .catch(() => {
                    this.rules = defaultRules;
                });
        },
        handleBackClick() {
            quitCurrentWebview();
        },
        goInvite() {
            openCommonWebViewByPath('/mobile-v2/act/invitation');

            this.$o2o({ e_c: '任务', e_a: '点击', e_n: '签到挑战页面点击邀请好友' });
        },
    },
};
</script>
